import axios from '../axios'


/* 
 * 
 */
export const findPage = () => {
    return axios({
        url: '/server/findPage',
        method: 'get'
    })
}


// id
export const loadServerIds = (data) => {
    return axios({
        url: '/server/serverIds',
        method: 'get',
        data
    })
}


// 
export const findCommands = (data) => {
    return axios({
        url: '/gameCmd/commands',
        method: 'get',
        data
    })
}

// 
export const execCommand = (data) => {
    return axios({
        url: '/gameCmd/exec',
        method: 'post',
        data
    })
}


